import React, { useState, useEffect } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import moment from 'moment-timezone';
import FreeSlotButton from './FreeSlotButton';
import axios from 'axios';
import Select from 'react-select';
import config from './../config.json';
import { Link } from 'react-router-dom';


const timezones = [
    { value: 'Asia/Calcutta', label: 'Asia Time (India)' },
    { value: 'Africa/Conakry', label: 'Africa/Conakry' },
    { value: 'America/Chicago', label: 'America/Chicago' },
    { value: 'Europe/Amsterdam', label: 'Europe/Amsterdam' },
    { value: 'America/Los_Angeles', label: 'America/Los_Angeles' },
];


const Calendar = () => {
    const [selectedDate, setSelectedDate] = useState(new Date());
    const [selectedSlot, setSelectedSlot] = useState(null);
    const [freeSlots, setFreeSlots] = useState([]);
    const [minuteDuration, setMinuteDuration] = useState(30);
    const [selectedTimezone, setSelectedTimezone] = useState(timezones[0]);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [bookingTitle, setBookingTitle] = useState('');
    const [error, setError] = useState(null);
    const currentDate = moment();

    // Function to handle the "Get Free Slots" button click
    const handleGetFreeSlots = async () => {
        try {
            const formattedSelectedDate = moment(selectedDate).format('YYYY-MM-DD');
            const timezoneValue = selectedTimezone ? selectedTimezone.value : 'Asia/Calcutta';
            const response = await axios.get(`${config.api.server}/api/free-slots?startDate=${formattedSelectedDate}&endDate=${formattedSelectedDate}&timezone=${timezoneValue}`); // Replace 'your-api-endpoint' with the actual API endpoint URL
            const slotsByDate = response.data;
            const allSlots = Object.values(slotsByDate).flat();
            setFreeSlots(allSlots);
            setError(null);
            setSelectedSlot(null);
            setIsModalOpen(false); // Close the modal after saving
        } catch (error) {
            console.error('Error fetching free slots:', error);
            setError('Error fetching free slots. Please try again later.');
            setFreeSlots([]);
            setIsModalOpen(false); // Close the modal after saving
        }
    };

    useEffect(() => {
        if (selectedDate || selectedTimezone) {
            handleGetFreeSlots();
        }
    }, [selectedDate, selectedTimezone]);

    const handleSlotSelect = (slot) => {
        setSelectedSlot(slot);
        setIsModalOpen(true);
    };

    const handleSaveBooking = async () => {
        try {
            setError(null);
            const response = await axios.post(`${config.api.server}/api/events`, {
                "title": bookingTitle,
                "startTime": selectedSlot,
                "duration": minuteDuration,
                "timezone": selectedTimezone ? selectedTimezone.value : 'America/Chicago'

            });

            setIsModalOpen(false); // Close the modal after saving
            setSelectedSlot(null); // Reset the selected slot after saving
            setBookingTitle(''); // Reset the booking title input after saving
            handleGetFreeSlots(); // Call this function again for refresh
        } catch (error) {
            console.error('Error saving booking:', error);
            setError('Error saving booking. Please try again.');
        }
    };

    const handleSelectedTimezoneChange = async (selectedOption) => {
        setSelectedTimezone(selectedOption && selectedOption.value ? selectedOption : '');
        setIsModalOpen(false); // Close the modal after saving
        // await handleGetFreeSlots();
    };

    return (
        <div className="container mt-5">
            <h1 className="mb-4">GoHighLevel - Booking App</h1>
            <div className="row">
                <div className="col-md-4 mb-3">
                    <DatePicker className="form-control"
                        selected={selectedDate}
                        onChange={(date) => setSelectedDate(date)}
                        dateFormat="MMMM d, yyyy"
                        minDate={currentDate.toDate()}
                        placeholderText="Select a date"
                    />
                </div>
                <div className="col-md-2 mb-3">
                    <input className="form-control"
                        type="number"
                        value={minuteDuration}
                        onChange={(e) => setMinuteDuration(e.target.value)}
                        disabled
                    />
                </div>
                <div className="col-md-3 mb-3">
                    <Select className="form-control"
                        value={selectedTimezone}
                        onChange={handleSelectedTimezoneChange}
                        options={timezones}
                        placeholder="Select timezone"
                    />
                </div>
                {freeSlots.length > 0 ? (
                    <div className="mt-4">
                        <h2>Available Slots:</h2>
                        <div className="row">
                            {freeSlots.map((slot, index) => (
                                <div key={index} className="col-md-3 mb-3">
                                    <div className="list-group">
                                        <FreeSlotButton className="list-group-item list-group-item-action"
                                            key={index}
                                            slot={slot}
                                            selectedTimezone={selectedTimezone}
                                            onSlotSelect={handleSlotSelect}
                                            minuteDuration={minuteDuration}
                                        />
                                    </div>
                                </div>
                            ))}
                            <hr />
                            {selectedSlot && (
                                <div>
                                    <p>
                                        <b>Selected Slot: As per (Asia/Calcutta)</b>{' '}
                                        {`${moment(selectedDate).format("YYYY-MM-DD")}_${new Date(selectedSlot).toLocaleTimeString()} - ${moment(selectedDate).format("YYYY-MM-DD")}_${new Date(
                                            new Date(selectedSlot).getTime() + minuteDuration * 60000
                                        ).toLocaleTimeString()}`}    </p>
                                    <p>
                                        <b>Selected Timezone:</b>{' '}
                                        {`${selectedTimezone.label}`}
                                    </p>
                                </div>
                            )}
                            {error && <p className="mt-4 text-danger">{error}</p>}

                            {isModalOpen && (
                                <div>
                                    <input
                                        className="form-control"
                                        type="text"
                                        value={bookingTitle}
                                        onChange={(e) => setBookingTitle(e.target.value)}
                                        placeholder="Enter booking title"
                                    />
                                    <button className="btn btn-primary" onClick={handleSaveBooking}>Save Booking</button>
                                </div>
                            )}

                        </div>
                    </div>
                ) : (
                    <p className="mt-4">No free slots available.</p>
                )}
                <Link to="/events">Show Events</Link>
            </div>
        </div>
    );
};

export default Calendar;