import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import config from '../config.json';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import moment from 'moment-timezone';

const EventList = () => {
    const [startDate, setStartDate] = useState(new Date());
    const [endDate, setEndDate] = useState(new Date());
    const [events, setEvents] = useState([]);
    const [error, setError] = useState(null);
    const currentDate = moment();

    // useEffect(() => {
    //     handleGetEvents();
    // });

    const handleGetEvents = async () => {
        try {
            setError(null);
            const formattedStartDate = startDate
                ? moment(startDate).format('YYYY-MM-DD')
                : '';
            const formattedEndDate = endDate
                ? moment(endDate).format('YYYY-MM-DD')
                : '';
            const response = await axios.get(
                `${config.api.server}/api/events?startDate=${formattedStartDate}&endDate=${formattedEndDate}`
            );

            const eventsData = response.data;
            setEvents(eventsData);
        } catch (error) {
            console.error('Error fetching events:', error);
            setError('Error fetching events. Please try again later.');
        }
    };

    return (
        <div className="container mt-5">
            <h1 className="mb-4">Event List</h1>
            <div className="row">
                <div className="col-md-4 mb-3">
                    <DatePicker
                        selected={startDate}
                        onChange={(date) => setStartDate(date)}
                        selectsStart
                        startDate={startDate}
                        endDate={endDate}
                        minDate={currentDate.toDate()}
                        className="form-control"
                        placeholderText="Start Date"
                    />
                </div>
                <div className="col-md-4 mb-3">
                    <DatePicker
                        selected={endDate}
                        onChange={(date) => setEndDate(date)}
                        selectsEnd
                        startDate={startDate}
                        endDate={endDate}
                        minDate={currentDate.toDate()}
                        className="form-control"
                        placeholderText="End Date"
                    />
                </div>
                <div className="col-md-3 mb-3">
                    <button className="btn btn-primary" onClick={handleGetEvents}>Show Events</button>
                </div>
                {events.length > 0 ? (
                    <div className="mt-4">
                        <h2>Events:</h2>
                        <table className="table">
                            <thead>
                                <tr>
                                    <th>Title</th>
                                    <th>Start Time</th>
                                    <th>End Time</th>
                                    <th>Timezone</th>
                                </tr>
                            </thead>
                            <tbody>
                                {events.map((event, index) => (
                                    <tr key={index}>
                                        <td>{event.title}</td>
                                        <td>{moment(event.startTime).format('YYYY-MM-DD HH:mm')}</td>
                                        <td>{moment(event.endTime).format('YYYY-MM-DD HH:mm')}</td>
                                        <td>{event.timezone}</td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                ) : (
                    <p>
                        {events.length > 0 ? (<b> No events found for the selected date range.</b>) : (<b></b>)}</p>
                )}

                {error && <p>{error}</p>}
            </div>

            <Link to="/">Go Back to Free Slots</Link>
        </div>
    );
};

export default EventList;
