import React from 'react';
import moment from 'moment-timezone';

const FreeSlotButton = ({ slot, onSlotSelect, minuteDuration, selectedTimezone }) => {
  const momentDate = moment(slot);
  momentDate.tz(selectedTimezone.value);

  return (
    <button onClick={() => onSlotSelect(slot)}>
      {moment(momentDate).format("HH:mm")}
    </button>
  );
};

export default FreeSlotButton;
